/**
 * module
 * 
 * **/
import server from "../util/fetch";
import { masterName } from "../config";
const App = {
  namespaced: true,
  state: {
    userinfo: null,
    blogList: [], // 
    detailURL: null // URL
  },
  actions: {
    async onLogin(context, payload) {
      console.log(":", payload.params);
      try {
        const msg = await new Promise((res, rej) => {
          setTimeout(() => {
            res({
              status: 200,
              message: "success",
              data: { id: 1, username: "admin", password: "123123" }
            });
          }, 1000);
        });
        console.log("", msg, context);
        if (msg.status === 200) {
          context.commit({
            type: "setUserinfo",
            userinfo: msg.data
          });
          console.log("");
          return msg;
        }
      } catch (e) {
        console.log("");
      }
    },
    /**  **/
    async getBlogList(context, payload) {
      try {
        const msg = await server(
          `https://api.github.com/repos/${masterName}/${masterName}.github.io/contents/blog`,
          null,
          "GET"
        );
        if (msg.status === 200 || msg.status === 304) {
          context.commit({
            type: "setBlogList",
            data: msg.data
          });
        }
        return msg;
      } catch (e) {
        console.log("");
      }
    },
    /**  **/
    async getBlogDetail(context, payload) {
      try {
        const url = `https://raw.githubusercontent.com/${masterName}/${masterName}.github.io/master/blog/${
          payload.url
        }`;
        const msg = await server(url, null, "GET");
        return msg;
      } catch (e) {
        console.log("");
      }
    },
    /** URL **/
    async saveDetailNow(context, payload) {
      try {
        context.commit({
          type: "setDetailURL",
          data: payload.data
        });
      } catch (e) {
        console.log("");
      }
    }
  },
  mutations: {
    setBlogList(state, payload) {
      console.log("mutaions:", payload);
      state.blogList = payload.data;
    },
    setUserinfo(state, payload) {
      console.log("", state, payload);
      state.userinfo = payload.userinfo;
    },
    setDetailURL(state, payload) {
      state.detailURL = payload.data;
    },
    clearUserinfo(state) {
      state.userinfo = null;
    }
  }
};

export default App;



