/**
 * module
 * 
 * **/
const Module = {
  namespaced: true,
  state: {
    playing: true // 
  },
  actions: {
    async setPlaying(context, payload) {
      console.log("playing:", payload.params);
      try {
        await context.commit({
          type: "setPlaying",
          playing: payload.params.playing
        });
      } catch (e) {
        console.log("");
      }
    }
  },
  mutations: {
    setPlaying(state, payload) {
      console.log("", payload);
      state.playing = payload.playing;
    }
  }
};

export default Module;



